


































































































































@import '@/styles/element-ui-new.scss';
@import '@/styles/public.scss';

.equip-box {
  padding: 0 20px;
  .list-item {
    display: flex;
    justify-content: space-between;
    min-height: 40px;
    line-height: 40px;

    .list-items {
      display: flex;
      flex: 1;

      .item-title,
      .item-content span {
        font-size: 12px;
      }

      .item-title {
        width: 100px;
        text-align: right;
        margin-right: 5px;
      }

      .item-content {
        flex: 1;
        span {
          color: #999999;
        }

        .content-text {
          line-height: 20px;
          margin-top: 10px;
        }

        .view-button {
          margin-left: 10px;
        }
        /deep/ .el-button {
          color: #4b77be;
          border-color: #4b77be;
        }
        /deep/ .el-button:hover {
          background: #4b77be;
          color: #ffffff;
        }

        .enclosure-box {
          .enclosure-item {
            display: flex;

            .enclosure-title,
            .enclosure-download,
            .enclosure-preview {
              padding: 0 10px;
            }
          }
        }
      }
    }
    .has-two-item {
      width: 46%;
    }
  }
}
