






































































































































































































































































































































































@import '@/styles/element-ui-new.scss';
@import '@/styles/public.scss';

.addTask-box {
  .content-box {
    height: 100%;
    .tabs-box {
      height: 100%;
      .basicInfo-box {
        padding: 0 40px;

        .list-item {
          display: flex;
          justify-content: space-between;
          height: 40px;
          line-height: 40px;

          .list-items {
            display: flex;
            flex: 1;

            .item-title,
            .item-content span {
              font-size: 12px;
            }

            .item-title {
              width: 100px;
              text-align: right;
              margin-right: 5px;
            }

            .item-content {
              span {
                color: #999999;
              }

              .view-button {
                margin-left: 10px;
              }
              /deep/ .el-button {
                color: #4b77be;
                border-color: #4b77be;
              }
              /deep/ .el-button:hover {
                background: #4b77be;
                color: #ffffff;
              }
            }
          }
          .has-two-item {
            width: 46%;
          }
        }
      }

      .equipmentInfo-box {
        padding: 0 40px;
        .table-box {
          margin: 0 0 10px 0;
          box-sizing: border-box;
        }
      }

      .inspectionPath-box {
        display: flex;
        justify-content: space-between;
        .map-box {
          width: 70%;
          height: 450px;
        }

        .inspectionPath-info-box {
          flex: 1;
          .inspectionPath-info-item {
            padding: 20px 15px;
            border-bottom: 1px dashed #0099cc;

            .inspectionPath-info-items {
              display: flex;
              height: 35px;
              line-height: 35px;
            }
          }
        }
      }
    }
  }
}
